.b-works-main {
  background: #f8f9f5;
  position: relative; }
  @media (max-width: 767px) {
    .b-works-main {
      padding-top: 20px; } }
  @media (min-width: 768px) and (max-width: 1199px) {
    .b-works-main {
      padding-top: 40px; } }
  @media (min-width: 1200px) {
    .b-works-main {
      padding-top: 58px; } }
  .b-works-main::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 100%;
    width: 100%;
    padding-top: 17%;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
    background-image: linear-gradient(to top right, #f8f9f5 0%, #f8f9f5 50%, transparent calc(50% + 1px), transparent 100%), linear-gradient(to top left, #f8f9f5 0%, #f8f9f5 50%, transparent calc(50% + 1px), transparent 100%);
    background-position: 0 bottom, 10% bottom;
    background-repeat: no-repeat; }

@media (max-width: 767px) {
  .b-works-main__title {
    margin-bottom: 20px; } }

@media (min-width: 768px) and (max-width: 1199px) {
  .b-works-main__title {
    margin-bottom: 25px; } }

@media (min-width: 1200px) {
  .b-works-main__title {
    margin-bottom: 37px; } }



